// stylelint-disable value-keyword-case
$font__main: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans,
  Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
// stylelint-enable value-keyword-case
$font__code: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
$font__pre: "Courier 10 Pitch", courier, monospace;
$font__line-height-body: 1.5;
$font__line-height-pre: 1.6;

// Fetch custom font variables
$font__headings__primary: CircularStdBlack, "sans-serif";
$font__headings__secondary: CircularStdBold, "sans-serif";
$font__text: "Open Sans", sans-serif;
$font__cta: CircularStdBook, "sans-serif";
